<template>
  <div class="home">
    <header>
      <div class="logo">
        LOGO
      </div>
      <div class="topmenu">
        <el-menu
          :default-active="topMenuActive"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#262626"
          text-color="#bbb"
          active-text-color="#fff">
          <el-menu-item v-for="item in topMenu" :key="item.index" :index="item.key">
            {{item.name}}
          </el-menu-item>
        </el-menu>      
      </div>
      <div class="user">
        admin
      </div>
    </header>
    <div class="content">
      <div style="width:100%;height:51px;"></div>
      <aside>
        <div class="aside-title">
          <p>用户</p><span class="btn-fold-menu" @click="btnFoldMenu"></span>
        </div>
        <div class="aside-menu">
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            background-color="#f0f6f6"
            text-color="#586376"
            active-text-color="#2277da">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>导航一</span>
              </template>
              <el-menu-item-group>
                <template slot="title">分组一</template>
                <el-menu-item index="1-1">选项1</el-menu-item>
                <el-menu-item index="1-2">选项2</el-menu-item>
              </el-menu-item-group>
              <el-menu-item-group title="分组2">
                <el-menu-item index="1-3">选项3</el-menu-item>
              </el-menu-item-group>
              <el-submenu index="1-4">
                <template slot="title">选项4</template>
                <el-menu-item index="1-4-1">选项1</el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-menu-item index="2">
              <i class="el-icon-menu"></i>
              <span slot="title">导航二</span>
            </el-menu-item>
            <el-menu-item index="3">
              <i class="el-icon-setting"></i>
              <span slot="title">导航三</span>
            </el-menu-item>
          </el-menu>          
        </div>
      </aside>
      <section></section>
    </div>
    <footer></footer>
  </div>
</template>
<script>
import Cookies from 'js-cookie'
import $ from 'jquery'
export default {
  name: 'index',
  data () {
    return {
      topMenu: [
        {
          name: '用户',
          index: '1',
          key: 'user'
        }
      ],
      topMenuActive: 'user',
      asideMenu: [
        {
          title: '微信管理',
          name: 'wx_manage',
          path: '/user/wx_manage'
        },
        {
          title: '权限管理',
          name: 'auth_manage',
          path: '/user/auth_manage'
        },
        {
          title: '用户管理',
          name: 'user_manage',
          path: '/user/用户管理'
        },
        {
          title: '微信',
          name: 'weixin',
          path: '/user/weixin'
        }
      ],
      asideActiveOpen: [],
      userName: ''
    }
  },
  mounted: function () {
  },
  methods: {
    clickAside () {
      let obj = document.getElementsByClassName('asideNav')[0].getElementsByClassName('el-menu-item')
      event.target.style.color = '#20a0ff'
      for (let i = 0; i < obj.length; i++) {
        if (event.target !== obj[i]) {
          obj[i].style.color = '#48576a'
        }
      }
    },
    logOut () {
      Cookies.remove('userName')
      Cookies.remove('token')
      window.location.href = ''
    },
    btnFoldMenu () {
      $('aside').animate({
        left: '-100px'
      }, 1000, function () {
        $('aside .btn-fold-menu').css({
          'border': '1px solid #c7e0ef',
          'border-left': 'none',
          'border-radius': '0 3px 3px 0',
          'background': 'url(static/top-menu.png) -87px -40px no-repeat #f0f6f6'
        })
        console.log($('aside .btn-fold-menu'))
      })
    }
  }
}
</script>
<style lang="less">
  @import "./home.less";
</style>


