import Login from '@/components/login/login'
import home from '@/components/index/home'

export const loginRoute = [
  {
    path: '/login',
    name: 'login',
    component: Login
  }, {
    path: '',
    component: Login,
    redirect: '/login'
  }
]

export const homeRoutes = [
  {
    path: '/home',
    name: 'home',
    component: home
  }, {
    path: '',
    component: home,
    redirect: '/home'
  }
]

export const routes = [
  ...loginRoute,
  ...homeRoutes
]



